export default{
    isMobile(){
        console.log('hello')
        let flag = navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i);
        return flag;
    },
    isMobileDevice() {//判断当前设备是否为移动端
        const ua = navigator.userAgent.toLowerCase();
        const t1 = /android|webos|iphone|ipad|ipod|blackberry|iemobile|opera mini/i.test(
            ua
        );
        const t2 = !ua.match("iphone") && navigator.maxTouchPoints > 1;
        return t1 || t2;
    },
    isMobilePhone() {//判断当前设备是手机
        const ua = navigator.userAgent.toLowerCase();
        const t1 = /android|webos|iphone|ipod|blackberry|iemobile|opera mini/i.test(
            ua
        );
        // const t2 = !ua.match("iphone") && navigator.maxTouchPoints > 1;
        return t1 ;
    },
    isIOS() {
        // const ua = navigator.userAgent;
        // return !!ua.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);
        var u = window.navigator.userAgent;
        var isAndroid = u.indexOf('Android') > -1 || u.indexOf('Linux') > -1; //g
        return !isAndroid;
    },
    // 获取屏幕宽高
    getScreenWidth(){
        return window.innerWidth || document.documentElement.clientWidth || document.body.clientWidth;//兼容性获取屏幕宽度
    },
    getScreenHeight(){
        return window.innerHeight || document.documentElement.clientHeight || document.body.clientHeight//兼容性获取屏幕高度
    }
}
